import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import './style.scss';

@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.dataForm(values);
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { form: { getFieldDecorator } } = this.props;
    return (
      <Form className="form-inline-label search-form" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="M SV/SBD">
              {getFieldDecorator('studentId', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>  
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



